import type { EventEmitter } from '@stencil/core';
import { Component, h, Prop, Watch, Element, State, Event } from '@stencil/core';

@Component({
	tag: 'o-checkbox',
	styleUrl: 'o-checkbox.scss',
	shadow: true,
	formAssociated: true
})
export class OCheckbox {
	private defaultValue: boolean;
	private valueHasChanged: boolean = false;

	@Element() el: HTMLElement;

	/** @prop {string} nameValue - The name attribute for the checkbox. Default is 'default'. */
	@Prop() nameValue: string = 'default';

	/** Determines the initial checked state of the checkbox. */
	@Prop({ mutable: true }) checked: boolean = false;

	/** Used for Storybook documentation purposes. */
	@Prop({ reflect: true }) label: string;

	/** @prop {boolean} disabled - Whether the input is disabled. Default is false. */
	@Prop({ reflect: true }) disabled = false;

	/** @prop {string} value - The value of the checkbox input. */
	@Prop({ reflect: true }) value: string;

	/** @prop {boolean} indeterminate - Whether the checkbox is in an indeterminate state. */
	@Prop({ mutable: true }) indeterminate: boolean = false;

	/** @prop {string} errorText - The error message to display when validation fails. */
	@Prop() errorText: string = '';

	/** @prop {boolean} required - Indicates if the checkbox is required */
	@Prop() required: boolean = false;

	/** Indicates if the input has an error */
	@State() hasError: boolean = false;

	/**
	 * @event oChange
	 */
	@Event() oChange: EventEmitter<{ checked: boolean }>;

	private internals: ElementInternals | null = null;

	constructor() {
		// Validation to allow unit tests to run without ElementInternals support
		try {
			if (typeof this.el.attachInternals === 'function') {
				this.internals = this.el.attachInternals();
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('ElementInternals not supported', e.message);
		}
	}

	connectedCallback() {
		this.validateInput(this.checked);
		/* istanbul ignore if */
		if (this.internals?.setFormValue) {
			try {
				this.internals.setFormValue(this.checked ? this.value || 'on' : null);
			} catch (e) {
				console.warn('Form value update not supported', e.message);
			}
		}
	}

	/** Returns the HTMLInputElement if found. */
	private getCheckboxElement(): HTMLInputElement | null {
		return this.el.shadowRoot?.querySelector('input[type="checkbox"]');
	}

	/** Watches for changes to the 'indeterminate' property. */
	@Watch('indeterminate')
	watchIndeterminate(newValue: boolean) {
		const checkbox = this.getCheckboxElement();
		if (checkbox) {
			checkbox.indeterminate = newValue;
		}
	}

	/** Watches for changes to the 'checked' property. */
	@Watch('checked')
	watchChecked(newValue: boolean) {
		if (!this.valueHasChanged && this.defaultValue === newValue) {
			return;
		}
		this.validateInput(newValue);
		const checkbox = this.getCheckboxElement();
		if (checkbox) {
			checkbox.checked = newValue;
			if (newValue) {
				this.indeterminate = false;
			}
			this.valueHasChanged = true;
		}

		/* istanbul ignore if */
		if (this.internals?.setFormValue) {
			try {
				this.internals.setFormValue(newValue ? this.value || 'on' : null);
			} catch (e) {
				/* istanbul ignore next */
				console.warn('Form value update not supported', e.message);
			}
		}
	}

	@Watch('errorText')
	errorTextChanged() {
		this.validateInput(this.checked);
		this.updateValidityState();
	}

	/** Validates the checkbox state */
	validateInput(isChecked: boolean) {
		this.hasError = !isChecked && this.errorText.trim() !== '';
		this.updateValidityState();
	}

	/** Update validity state for form association */
	private updateValidityState() {
		if (!this.internals) return;
		// Allow unit tests to run without ElementInternals support
		try {
			/* istanbul ignore if */
			if (this.hasError) {
				this.internals.setValidity?.({ customError: true }, this.errorText, this.el.shadowRoot.querySelector('input'));
			} else {
				this.internals.setValidity?.({});
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('Validity state update not supported', e.message);
		}
	}

	/** It sets the default value and initial indeterminate state of the checkbox element */
	componentDidLoad() {
		this.defaultValue = this.checked;
		const checkbox = this.getCheckboxElement();
		if (checkbox) {
			checkbox.indeterminate = this.indeterminate;
		}
		this.validateInput(this.checked);
	}

	/** Handle checkbox change */
	handleChange(event: Event) {
		const checkbox = event.target as HTMLInputElement;
		this.checked = checkbox.checked;
		this.validateInput(this.checked);
		if (this.internals?.setFormValue) {
			this.internals.setFormValue(this.checked ? this.value || 'on' : null);
		}
		this.oChange.emit({ checked: this.checked });
	}

	render() {
		/** Generates CSS classes for the disabled state of the checkbox. */
		const disabledClasses = this.disabled
			? 'border-form-color-border-disabled bg-form-color-background-disabled cursor-not-allowed checked:border-form-color-border-disabled checked:bg-form-color-background-disabled  checked:disabled:bg-background-brand-knockout-disabled indeterminate:disabled:bg-background-brand-knockout-disabled'
			: '';

		const errorClasses = this.hasError ? 'border-border-status-error' : '';

		return (
			<div class="flex flex-col body-default antialiased">
				<div class="flex items-center">
					<input
						class={`appearance-none bg-form-color-background-default checked:bg-background-brand-knockout checked:hover:bg-background-brand-knockout checked:focus:bg-background-brand-knockout indeterminate:bg-background-brand-knockout indeterminate:focus:bg-background-brand-knockout indeterminate:hover:bg-background-brand-knockout rounded-radius-rounded border-form-color-border-default border-width-sm focus:outline-width-lg focus:outline-focus-ring-color-border-default focus:outline-offset-0 focus-visible:outline-width-lg focus-visible:ring-focus-ring-color-border-default ${disabledClasses} ${errorClasses}`}
						id={this.nameValue}
						type="checkbox"
						checked={this.checked}
						disabled={this.disabled}
						value={this.value}
						required={this.required}
						onChange={(event: Event) => this.handleChange(event)}
					/>
					<span class="ml-2 flex items-center">
						<o-label
							htmlFor={this.nameValue}
							label={this.label}
							variant={this.hasError ? 'error' : this.disabled ? 'disabled' : undefined}
							required={this.required}
						></o-label>
					</span>
				</div>
				{this.hasError && (
					<div class="body-default mt-1">
						<span class="text-content-status-error">{this.errorText}</span>
					</div>
				)}
			</div>
		);
	}
}
